export default {
    data: () => {
        return {
            Nom: "Undefined",
            Description: ''
        }
    },
    methods: {
        async submit() {
            try {
                const body = {
                    Nom: this.Nom,
                    Description: this.Description
                }
                const request = await this.$axios.$post('http://localhost:8000/editIngredient',body)
                return request
            } catch (error) {
                this.error = error
            }
        },
        goBack() {
            this.$router.back()
        }
    }
}
